
import { defineComponent, PropType } from 'vue'
import menuListData from '@/store/modules/menuListData';
import { MenuInfo } from './menuType'
import { DataCenter } from '@/shared/services/DataCenter';
export default defineComponent({
    props: {
        staticScreenName: {
            type: String,
            required: false,
        },
        queryData: {
            type: Array as PropType<any[]>,
            required: false,
        }
    },
    data() {
        return {
            breadcrumbs: [] as MenuInfo[],
            menuList: [] as MenuInfo[]
        };
    },
    async created() {
        setTimeout(() => {
            this.renderMenuFromState();
            this.getMenuList();
        }, 100);
    },
    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
    },
    watch: {
        staticScreenName(newValue) {
            // TODO: CHECK Configuration Management screene
            if (this.$route.path == '/COM210001SC00') {
                if (newValue) {
                    this.breadcrumbs.length == 2 ? this.breadcrumbs : this.breadcrumbs.pop()
                }
                this.breadcrumbs.push({ menuName: newValue, route: '' })
            }
        }
    },
    methods: {
        pathDriver(currentPath: string, queryItem?: any) {
            if (this.approvalType === '') {
                if (this.$route.path != currentPath) {
                    if (queryItem) {
                        this.$router.push({
                            path: currentPath,
                            query: queryItem
                        });
                    } else {
                        this.$router.push(currentPath);
                    }
                } else {
                    return;
                }
            }
        },
        async renderMenuFromState() {
            return menuListData.getters.getMenuData;
        },
        async getMenuList() {
            try {
                this.menuList = await this.renderMenuFromState();
                if (this.menuList.length > 0) {
                    this.menuList = JSON.parse(JSON.stringify(this.menuList)).filter((item) => item.parentMenuID.replace(/\s/g, '') != "");
                    this.buildBreadcrumb(this.menuList, this.$route.path);
                } else {
                    console.log('menu not yet rendered');
                }
            } catch (error) {
                console.log('Error rendering menu:', error);
            }
        },

        async buildBreadcrumb(data: any, currentRoute: string) {
            // TODO: CHECK EXISTING SCREEN-ID
            const currentMenu: any = data.find((item: MenuInfo) => `/${item.screenID}` == currentRoute);
            if (currentMenu) {
                this.generateBreadcrumbs(data, currentMenu);
                if (this.$route.path == '/ICM201001SC00') {
                    const menu: any = data.find((item: MenuInfo) => item.screenID == 'ICM201001SC00');
                    DataCenter.setData("SpecialScreenID", JSON.stringify(menu))
                }
            } else {
                // TODO: CHECK SCREEN-ID SUB USER SCREEN
                if (this.$route.path == '/CIB202003SC00') {
                    let menuitem;
                    let parentSubUserListScreen: any;
                    parentSubUserListScreen = data.find((item: any) => item.screenID == 'CIB202002SC00');
                    menuitem = { menuName: this.staticScreenName ? this.staticScreenName : '', screenID: this.$route.path.replace("/", ''), parentMenuID: parentSubUserListScreen.menuID, menuID: '1' };
                    this.generateBreadcrumbs(data, menuitem);
                }
                const notExistScreenID: string = this.$route.path.replace("/", '')
                let menu = [] as MenuInfo[];
                this.menuList.forEach((item: any) => {
                    menu.push(item.screenID)
                });
                menu.forEach((parentScreenID) => {
                    // TODO: COMPARE TEXT BEFORE LAST TEXT
                    let menu: any;
                    if (this.matchScreenID(parentScreenID, notExistScreenID)) {
                        menu = data.find((item: MenuInfo) => `${item.screenID}` == parentScreenID);
                        if (menu) {
                            this.generateBreadcrumbs(data, menu);
                        }

                    }
                    if (this.approvalType !== '') {
                        if (notExistScreenID == 'VBA202002SC00' || notExistScreenID == 'CIB201003SC00') {
                            if (this.matchApprovalModeScreenID(parentScreenID, notExistScreenID)) {
                                const menu: any = data.find((item: MenuInfo) => `${item.screenID}` == parentScreenID);
                                this.generateBreadcrumbs(data, menu);
                            }
                        }

                    }
                })
                // TODO: CHECK THE LAST TEXT OF SCREENID TO ADD STATIC NAVIGATION (REGISTER/EDIT SCREEN)
                let lastCharacterOfScreenID = notExistScreenID.charAt(notExistScreenID.length - 1);
                if (lastCharacterOfScreenID === '1') {
                    let staticBreabCrumb: MenuInfo = { menuName: this.staticScreenName ? this.staticScreenName : '', route: `/${notExistScreenID}` };
                    this.breadcrumbs.push(staticBreabCrumb)
                    DataCenter.setData("TempBreadCrumb", JSON.stringify(staticBreabCrumb))
                } else if (lastCharacterOfScreenID === '2') {
                    if (this.$route.path == '/COM211001SC02') {
                        this.breadcrumbs.push({ menuName: this.staticScreenName ? this.staticScreenName : '', route: `/${notExistScreenID}` })
                    } else {
                        let previousStaticScreenID = JSON.parse(await DataCenter.getData('TempBreadCrumb') as any);
                        this.breadcrumbs.push(previousStaticScreenID, { menuName: this.staticScreenName ? this.staticScreenName : '', route: `/${notExistScreenID}` })
                    }

                } else {
                    // TODO:  CHECK ON SCREEN ID IS NOT EQUAL TO NEW EVENT SCREEN
                    if (this.$route.path != "/CIB202003SC00" && this.$route.path != '/PUM202002SC00') {
                        let previousPath = this.$router.options.history.state.back as string;
                        let pathID = previousPath.includes('/') ? previousPath.replace("/", '') : previousPath;
                        const menuItems: MenuInfo = await data.find((item: MenuInfo) => `${item.screenID}` == pathID);
                        this.breadcrumbs.push({ menuName: this.staticScreenName ? this.staticScreenName : '', route: `/${notExistScreenID}` })
                        this.generateBreadcrumbs(data, menuItems);
                    }
                }
                // TODO:  CHECK ON NEW EVENT SCREEN
                if (this.$route.path == "/ICM201002SC01") {
                    this.breadcrumbs = [];
                    let menuNewEvent = JSON.parse(await DataCenter.getData('SpecialScreenID') as any);
                    this.breadcrumbs.push({ menuName: this.staticScreenName ? this.staticScreenName : '', route: `/${notExistScreenID}` })
                    this.generateBreadcrumbs(data, menuNewEvent);
                } else if (this.$route.path == '/CIB202003SC01') {
                    // TODO:  CHECK ON SUB USER REGISTER OR EDIT SCREEN
                    this.breadcrumbs = [];
                    let menuitem;
                    let parentSubUserListScreen: any;
                    parentSubUserListScreen = data.find((item: any) => item.screenID == 'CIB202002SC00');
                    menuitem = { menuName: this.$t('cib202003sc00.sub_user_list'), screenID: 'CIB202003SC00', parentMenuID: parentSubUserListScreen.menuID };
                    this.breadcrumbs.push({ menuName: this.staticScreenName ? this.staticScreenName : '', route: `/${notExistScreenID}` })
                    this.generateBreadcrumbs(data, menuitem);
                } else if (this.$route.path == '/PUM202002SC00') {
                    let menuitem;
                    let homeScreen: any;
                    homeScreen = data.find((item: any) => item.screenID == 'COM200001SC00');
                    menuitem = { menuName: this.staticScreenName ? this.staticScreenName : '', screenID: this.$route.path.replace("/", ''), parentMenuID: homeScreen.menuID };
                    this.generateBreadcrumbs(data, menuitem);
                }
            }
        },

        async generateBreadcrumbs(data: any, menu: MenuInfo) {
            try {
                if (menu) {
                    const breadcrumb = { menuName: menu.menuName, route: `/${menu.screenID}`, menuIconID: menu.menuIconID } as any;
                    this.breadcrumbs.unshift(breadcrumb);
                    if (menu.parentMenuID) {
                        const parentMenu = data.find((item: MenuInfo) => item.menuID == menu.parentMenuID);
                        if (parentMenu) {
                            this.generateBreadcrumbs(data, parentMenu);
                        }
                    }
                }

            } catch (error) {
                console.log(error);
            }
        },

        matchScreenID(screenA, screenB) {
            if (screenA.length !== screenB.length) {
                return false;
            }
            for (let i = 0; i < screenA.length - 1; i++) {
                if (screenA[i] !== screenB[i]) {
                    return false;
                }
            }
            return true;
        },

        matchApprovalModeScreenID(screenA, screenB) {
            if (screenA.length !== screenB.length) {
                return false;
            }
            for (let i = 0; i < screenA.length - 5; i++) {
                if (screenA[i] !== screenB[i]) {
                    return false;
                }
            }
            return true;
        },

    }
})
