
import { PropType, defineComponent } from "vue";
import { ModifyCard } from './ModifyCard.type';

export default defineComponent({
  name: "modify-card",
  props: {
    data: {
      type: Object as PropType<ModifyCard>,
      required: true
    }
  }, 
  computed: {
    createdEmployee(): string {
      // return this.data.registerTellerId?.trim() && this.data.registerTellerName?.trim() ? `${this.data.registerTellerId} (${this.data.registerTellerName})` : ''
      return `${this.data.registerTellerName || ''} ${this.data.registerTellerId?.trim() ? `(${this.data.registerTellerId})` : ''}`
    },
    createdDate(): string {
      return `${this.$datetime.formatStringToTime(this.data.registerTime ? this.data.registerTime : '')} ${this.$datetime.formatStringToStringDate(this.data.registerDate ? this.data.registerDate : '')}`
    },
    updatedEmployee(): string {
      // return this.data.updatedTellerId?.trim() && this.data.updatedTellerName?.trim() ? `${this.data.updatedTellerId} (${this.data.updatedTellerName})` : ''
      return `${this.data.updatedTellerName || ''} ${this.data.updatedTellerId?.trim() ? `(${this.data.updatedTellerId})` : ''}`
    },
    updatedDate(): string {
      return `${this.$datetime.formatStringToTime(this.data.updatedTime ? this.data.updatedTime : '')} ${this.$datetime.formatStringToStringDate(this.data.updatedDate ? this.data.updatedDate : '')}`
    },
    deletedEmployee(): string {
      // return this.data.deleteTellerId?.trim() && this.data.deleteTellerName?.trim() ? `${this.data.deleteTellerId} (${this.data.deleteTellerName})` : ''
      return `${this.data.deleteTellerName || ''} ${this.data.deleteTellerId?.trim() ? `(${this.data.deleteTellerId})` : ''}`
    },
    deletedDate(): string {
      return `${this.$datetime.formatStringToTime(this.data.deleteTime ? this.data.deleteTime : '')} ${this.$datetime.formatStringToStringDate(this.data.deleteDate ? this.data.deleteDate : '')}`
    },
    isShowRegister(): boolean{
      return this.data.registerDate?.trim() || this.data.registerTime?.trim() || this.data.registerTellerId?.trim() || this.data.registerTellerName?.trim() ? true : false
    },
    isShowUpdate(): boolean{
      return this.data.updatedDate?.trim() || this.data.updatedTime?.trim() || this.data.updatedTellerId?.trim() || this.data.updatedTellerName?.trim() ? true : false
    },
    isShowDelete(): boolean{
      return this.data.deleteDate?.trim() || this.data.deleteTime?.trim() || this.data.deleteTellerId?.trim() || this.data.deleteTellerName?.trim() ? true : false
    }
  }
})
