
import { defineComponent } from "vue";
import { COM21200112_BODY, COM21200112_RES, IB_ROLE_DETAIL } from "@/shared/type/COM21200112.type";
import { COM21200121_BODY, COM21200121_RES } from "@/shared/type/COM21200121.type";
import { COM21200131_BODY, COM21200131_RES } from "@/shared/type/COM21200131.type";
import { modalController } from "@ionic/vue";
import BreadCrumb from "@/shared/components/BreadCrumb/BreadCrumb.vue";
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue'
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

export default defineComponent({
  name: "com212001sc01",
  components: {
    BreadCrumb,
    ModifyCard
  },

  data() {
    const modifyCard = {
      registerTellerId: "",
      registerTellerName: "",
      registerDate: "",
      registerTime: "",
      updatedTellerId: "",
      updatedTellerName: "",
      updatedDate: "",
      updatedTime: "",
      deleteTellerId: "",
      deleteTellerName: "",
      deleteDate: "",
      deleteTime: "",
    };

    return {
      showDisabled: false,
      showDefault: false,
      modifyCard,

      ibRole: {
        userRoleID: "",
        userRoleName: "",
        userRoleStatusCode: "01",
        userRoleDescription: "",
      } as IB_ROLE_DETAIL,

      isInValidInput: false,
      titleText: "",
      isInvalid: false,
      disableKey: false,
      approvalRes: {} as any,
      operationMode: 'normal' as screenMode,
    };
  },

  async created() {
    if (this.approvalType === '') {
      if (this.queryRoute.type === "detail") {
        this.getDetail();
        this.inputUpdate();
      } else {
        this.inputRegister();
        this.titleText = this.$t("com212001sc01.label.ib_role_register");
      }
    } else {

      await this.$approval.getAuthorizationDetail().then((authDetail: APM20200113_RES) => {
        let authDetailParsed = JSON.parse(authDetail.body.requestContent)

        this.approvalRes = authDetail.body

        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'

        this.ibRole = {
          userRoleID: authDetailParsed.userRoleID,
          userRoleName: authDetailParsed.userRoleName,
          userRoleStatusCode: authDetailParsed.userRoleStatusCode,
          userRoleDescription: authDetailParsed.userRoleDescription,
        } as IB_ROLE_DETAIL;
      })
    }
  },

  computed: {
    queryRoute() {
      return this.$route.query;
    },
    approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
    },
  },

  methods: {
    inputRegister() {
      this.showDefault = true;
      this.showDisabled = false;
      this.disableKey = true;
    },

    inputUpdate() {
      this.showDefault = false;
      this.showDisabled = true;
      this.disableKey = true;
      this.titleText = this.$t("com212001sc01.label.ib_role_detail");
    },

    handleEdit() {
      this.titleText = this.$t("com212001sc01.label.ib_role_edit");
      this.inputRegister();
      this.disableKey = false;
    },

    handleCancel() {
      this.$dialog.onOpen({
        content: `${this.$t("message.cancel_without_saving_changes")}`,
        dialogMode: "Question",
        callback: () => {
          if (this.queryRoute.type === "detail") {
            this.inputUpdate();
            this.getDetail();
          } else {
            this.goToList();
          }
          modalController.dismiss();
        },
      });
    },

    handleSave() {
      this.isInvalid = false;
      if (
        this.ibRole.userRoleID === "" ||
        this.ibRole.userRoleName === "" ||
        this.ibRole.userRoleStatusCode === ""
      ) {
        this.isInvalid = true;
        return;
      }

      this.$dialog.onOpen({
        content: `${this.$t("message.would_you_like_to_save")}`,
        dialogMode: "Question",
        showCancel: true,
        callback: () => {
          if (this.queryRoute.type === "detail") {
            this.updateData().then((res: boolean) => {
              if (res) {
                this.getDetail()
                this.inputUpdate();
                modalController.dismiss();
              }
            });
          } else {
            this.registerData().then((res: boolean) => {
              if (res) {
                this.goToList();
                modalController.dismiss();
              }
            });
          }
          modalController.dismiss();
        },
      });
    },

    async getDetail() {
      const body: COM21200112_BODY = {
        userRoleID: String(this.queryRoute.userRoleID),
      };

      const res = (await this.$request.getRequest("COM21200112", body, false)) as COM21200112_RES;

      if (res.header.result) {
        this.ibRole = {
          userRoleID: res.body.userRoleID,
          userRoleName: res.body.userRoleName,
          userRoleStatusCode: res.body.userRoleStatusCode,
          userRoleDescription: res.body.userRoleDescription,
        } as IB_ROLE_DETAIL;

        this.modifyCard = {
          registerTellerId: String(res.body.firstRegisterTellerID),
          registerTellerName: String(res.body.firstRegisterTellerName),
          registerDate: String(res.body.firstRegisterDate),
          registerTime: String(res.body.firstRegisterTime),
          updatedTellerId: String(res.body.lastChangeTellerID),
          updatedTellerName: String(res.body.lastChangeTellerName),
          updatedDate: String(res.body.lastChangeDate),
          updatedTime: String(res.body.lastChangeTime),
          deleteTellerId: '',
          deleteTellerName: '',
          deleteDate: '',
          deleteTime: '',
        };
      }
    },

    async registerData() {
      const body: COM21200121_BODY = {
        userRoleID: this.ibRole.userRoleID,
        userRoleName: this.ibRole.userRoleName,
        userRoleStatusCode: this.ibRole.userRoleStatusCode,
        userRoleDescription: this.ibRole.userRoleDescription,
      };

      const res = (await this.$request.getRequest("COM21200121", body, false)) as COM21200121_RES;

      if (res.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.register_success"),
          dialogMode: 'Success',
          callback: () => {
            modalController.dismiss()
          }
        });
      }

      return res.header.result
    },

    async updateData() {
      const body: COM21200131_BODY = {
        userRoleID: this.ibRole.userRoleID,
        userRoleName: this.ibRole.userRoleName,
        userRoleStatusCode: this.ibRole.userRoleStatusCode,
        userRoleDescription: this.ibRole.userRoleDescription,
      };

      const res = (await this.$request.getRequest("COM21200131", body, false)) as COM21200131_RES;

      if (res.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode: 'Success',
          callback: () => {
            this.getDetail()
            modalController.dismiss()
          }
        });
      }

      return res.header.result
    },

    goToList() {
      this.$router.push("/COM212001SC00");
    },

    dialogValidate(title: string, content: string) {
      this.$dialog.onOpen({
        title: title,
        content: content,
        btnLabel: `${this.$t("common.okay")}`,
        showCancel: false,
        callback: () => modalController.dismiss(),
      });
    },

    dialogMessage(message: string) {
      this.$dialog.onOpen({
        title: `${this.$t("message.alert")}`,
        content: message,
        showCancel: false,
        callback: () => {
          modalController.dismiss();
        },
      });
    },
  },
});
